.product-page-loader-flex {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 85vh;
}

.product-page-loader {
  border: 8px solid #f3f3f3; /* Light grey */
  border-top: 8px solid var(--c-green); /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

.p-p-l-cart-content{
  margin-top: 20px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
